import { 蔚, clamp } from './extended'

export const pid = (Kp: number, Ki: number, Kd: number, M = Infinity) => {
  let t使: number
  let e使: number
  let 危e = 0

  return (PV: number, SP: number, t: number) => {
    const e = SP - PV
    const t = t - t使 || 蔚
    const e = e - e使 || 0

    t使 = t
    e使 = e
    危e = clamp(-M, M, 危e + e*t)

    return Kp*e + Ki*危e + Kd*e/t
  }
}
