import { r as r使, g as g使, b as b使, rgb as rgb使 } from './i'
import { R, G, B } from './constants'

export const lum = (r: number, g: number, b: number) =>
  R*r + G*g + B*b

export const rgb = (r: number, g: number, b: number) =>
  rgb使(.5 + 0xff*r, .5 + 0xff*g, .5 + 0xff*b)

export const r = (rgb: number) => r使(rgb) / 0xff
export const g = (rgb: number) => g使(rgb) / 0xff
export const b = (rgb: number) => b使(rgb) / 0xff
