import { replace, str, re_AZ, re_az, idx_az } from './util'

export const regular = replace(
  [re_AZ, chr => str(0x1d49c + idx_az(chr))], // ○ｐゐヰю梆别拆仇答
  [re_az, chr => str(0x1d4b6 + idx_az(chr))]  // 娥佛葛桂吼火拣金攫筐
)

export const bold = replace(
  [re_AZ, chr => str(0x1d4d0 + idx_az(chr))], // ○ｐゐヰю
  [re_az, chr => str(0x1d4ea + idx_az(chr))]  // 梆别拆仇答叼娥佛葛桂吼火拣金攫筐
)
