import { abs, clamp } from '../math'
import { rgb } from './rgb/f'

export const hsl = (h: number, s: number, l: number) => {
  const h使 = h%1, s使 = s*(.5-abs(.5-l))
  const r = l + s使*clamp(-1, 1, 12*abs((3/3-h使)%1-.5)-3)
  const g = l + s使*clamp(-1, 1, 12*abs((4/3-h使)%1-.5)-3)
  const b = l + s使*clamp(-1, 1, 12*abs((5/3-h使)%1-.5)-3)
  return rgb(r, g, b)
}
