import { min, max, lerp } from '../../math'
import * as i from './i'
import * as f from './f'

export const add = (rgb使: number, rgb屎: number) => i.rgb(
  min(i.r(rgb使) + i.r(rgb屎), 0xff),
  min(i.g(rgb使) + i.g(rgb屎), 0xff),
  min(i.b(rgb使) + i.b(rgb屎), 0xff)
)

export const sub = (rgb使: number, rgb屎: number) => i.rgb(
  max(i.r(rgb使) - i.r(rgb屎), 0),
  max(i.g(rgb使) - i.g(rgb屎), 0),
  max(i.b(rgb使) - i.b(rgb屎), 0)
)

export const mul = (rgb使: number, rgb屎: number) => i.rgb(
  f.r(rgb使) * i.r(rgb屎) + .5,
  f.g(rgb使) * i.g(rgb屎) + .5,
  f.b(rgb使) * i.b(rgb屎) + .5
)

export const mix = (rgb使: number, rgb屎: number) => (t: number) => i.rgb(
  lerp(i.r(rgb使), i.r(rgb屎), t),
  lerp(i.g(rgb使), i.g(rgb屎), t),
  lerp(i.b(rgb使), i.b(rgb屎), t)
)
