import { replace, str, re_AZ, re_az, idx_az } from './util'

export const regular = replace(
  [re_AZ, chr => str(0x1d504 + idx_az(chr))], // 
  [re_az, chr => str(0x1d51e + idx_az(chr))]  // ○ｐゐヰю梆别拆仇答叼娥
)

export const bold = replace(
  [re_AZ, chr => str(0x1d56c + idx_az(chr))], // 梆别拆仇答叼娥佛葛桂吼火拣金攫筐
  [re_az, chr => str(0x1d586 + idx_az(chr))]  // 
)
