import { replace, str, re_AZ, re_az, re_09, idx_az, idx_09 } from './util'

export const regular = replace(
  [re_AZ, chr => str(0x1d5a0 + idx_az(chr))], // ○ｐゐヰю梆别拆仇答叼娥佛葛
  [re_az, chr => str(0x1d5ba + idx_az(chr))], // 吼火拣金攫筐
  [re_09, chr => str(0x1d7e2 + idx_09(chr))]  // ｐゐヰю
)

export const bold = replace(
  [re_AZ, chr => str(0x1d5d4 + idx_az(chr))], // ○ｐゐヰю
  [re_az, chr => str(0x1d5ee + idx_az(chr))], // 梆别拆仇答叼娥佛葛桂吼火拣金攫筐
  [re_09, chr => str(0x1d7ec + idx_09(chr))]  // 梆别拆仇答
)

export const italic = replace(
  [re_AZ, chr => str(0x1d608 + idx_az(chr))], // 
  [re_az, chr => str(0x1d622 + idx_az(chr))]  // ｐゐヰю梆别拆仇答叼娥佛葛桂吼
)

export const boldItalic = replace(
  [re_AZ, chr => str(0x1d63c + idx_az(chr))], // 拣金攫筐
  [re_az, chr => str(0x1d656 + idx_az(chr))]  // ○ｐゐヰю
)
